/* from bootstrap template */
/*
 * Globals
 */

/* Links */
a,
a:focus,
a:hover {
  color: black;
}

/* Custom default button */
.btn-default,
.btn-default:hover,
.btn-default:focus {
  color: #333;
  text-shadow: none; /* Prevent inheritence from `body` */
  background-color: #fff;
  border: 1px solid #fff;
}


/*
 * Base structure
 */

html,
body {
  height: 100vh;
  background-color: #f5f3ef;
}
body {
  color: black;
  text-align: center;
  text-shadow: 0 1px 3px rgba(0,0,0,.5);
}

/* Extra markup and styles for table-esque vertical and horizontal centering */
.site-wrapper {
  display: table;
  width: 100%;
  height: 100%;
  background-color: #333;
  height: 100%; 
  min-height: 100%;
}
.site-wrapper-inner {
  display: table-cell;
  vertical-align: top;
}
.cover-container {
  margin-right: auto;
  margin-left: auto;
}

/* Padding for spacing */
.inner {
  padding: 30px;
}

.main-section {
  padding-top: 3%;
  margin-bottom: 5vh;
  text-align: center;
  font-family: Montserrat, Times, serif;
}

/* Things Anneliese is adding */
.main-section .cover-heading {
  /* text-align: center;
  font-family: 'Times New Roman', Times, serif; */
  color: rgb(74, 47, 3);
  font-size: 9vh;
  text-shadow: none;
}

.main-section .check-text{
  font-size: 5vh;
  color: rgb(74, 47, 3);
  text-shadow: none;
}
.main-section .calendar {
  border: solid 0px #777;
  width: "800";
  height: "600";   
  scroll-behavior: none;
  font-family: 'Times New Roman', Times, serif;
}
