:root {
  --online: #44b700;
  --offline: #b74400;
}

body {
  font-family: 'Roboto';
  font-size: 16px;
 
}

.App {
  width: 100%;
  min-height: 100vh;
  background-image: url("vubackground.png");
  background-attachment: fixed;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.main-wrapper {
  width: 100%;
}

.error {
  margin: 0.5rem;
  color: red;
  text-align: center;
}



